* {
    margin: 0;
    box-sizing: border-box;
}

//styling

html {
    font-family: "Nunito Sans", sans-serif;
    font-size: 16px;
    overflow-x: hidden;
    background-color: $black-900;
    color: $white;
}

.top-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $shadow-black-transparent;
    backdrop-filter: blur(0.5rem);
    width: 100%;
    height: 3.5rem;
    padding: 0 1rem;
    position: fixed;
    z-index: 2;
}
#logo,
#dropdown-menu-icon {
    display: block;
    width: 1.75rem;
    height: 1.75rem;
    font-size: 1.75rem;
    transition: ease-in-out 0.2s;
    cursor: pointer;
    &:hover {
        color: $grey-500;
    }
}
#chevron-to-show-social-menu {
    display: none;
}
.nav-desktop {
    display: none;
}

//toggle menu

.toggle-menu {
    display: none;
    position: fixed;
    width: 100%;
    height: auto;
    background-color: $black-600;
    transform: translateY(3.5rem);
    color: $grey-500;

    border-radius: 0 0 5% 5%;
    box-shadow: 1px 3px 15px $shadow-black-transparent;
    opacity: 0;
}

.toggle-menu.active {
    transition: 0.2s ease-in-out;
    opacity: 1;
    display: block;
    z-index: 3;
}

.all-inside {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    padding: 1rem;
}

.toggle_menu-a {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 2rem;
    font-size: 1.2rem;
    text-transform: uppercase;
    font-weight: 800;
}
.nav-a-toggle-menu {
    text-decoration: none;
    color: $amulet-green-400;
    transition: 0.1s ease-in-out;
    &:hover {
        transform: scale(1.05);
        color: $grey-600;
    }
}

//right side toggle menu
.toggle-column-right {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: auto;
    gap: 0.5rem;
}

#toggle-menu-a-1,
#toggle-menu-a-2,
#toggle-menu-a-3,
#toggle-menu-a-4,
#toggle-menu-a-5,
#toggle-menu-a-6,
#toggle-menu-a-7,
#toggle-menu-a-8,
#toggle-menu-a-9 {
    color: $white;
    font-size: 1.75rem;
    transition: 0.1s ease-in-out;

    &:hover {
        transform: scale(1.2);
        color: $grey-500;
    }
}

/* bottom navigation */
.bottom-navigation-only {
    height: 49.8%;
    width: 100vw;
}

.bottom-nav-ul {
    display: flex;
    justify-content: left;
    align-items: center;
    flex-direction: row;
    list-style: none;
    font-size: 0.9rem;
    padding: 0 1.5rem;
    width: 100vw;
    height: auto;
}

.bottom-li-nav {
    margin: 1rem;
    gap: 1.5rem;
}

#bottom-li-nav-about,
#bottom-li-nav-jobs,
#bottom-li-nav-appren {
    cursor: pointer;
}

#bottom-li-nav-about {
    color: $amulet-green-400;
}
.nav-a-social {
    display: none;
}
//section 1

.section-1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    padding: 4rem 0.5rem 0 0.5rem;
    /* z-index: 1; */
}

.section-1-card {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    height: auto;
    padding: 2rem 1rem;
    /* z-index: 1; */
    background-color: $shadow-black;
    border-radius: 1rem;
}
//name and logo
.link-to-story {
    text-decoration: none;
    color: $white;
    cursor: pointer;
}

.name-and-logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    width: 100%;
    height: auto;
    gap: 1.3rem;
    line-height: 3.8rem;
}
.h1-main-name {
    color: $white;
    font-weight: 800;
    width: auto;
    font-size: 1.9rem;
    &:hover,
    :focus,
    :active {
        color: $eastern-blue-500;
    }
}
.img-square-section {
    width: 1.9rem;
    height: 1.9rem;
}

//introduction
.dot-and-introduction {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    /*  width: 100%;
    height: auto; */
    gap: 0.5rem;
    line-height: 1.6rem;
}
.p_introduction {
    display: flex;
    justify-items: center;
    align-items: center;
    font-size: 0.8rem;
    color: $grey-600;
}
#dot-introduction {
    color: $amulet-green-400;
    font-size: 0.6rem;
}
//main description

.p_description {
    line-height: 2rem;
    font-size: 1rem;
    font-weight: 500;
}
.hr-line-to-p {
    border: none;
    width: 100%;
    background-color: $grey-700;
    height: 0.05rem;
    opacity: 0.1;
    margin: 0.5rem 0;
}

.name-in-p-green {
    color: $amulet-green-400;
    font-weight: 700;
}
.name-in-p-blue {
    color: $eastern-blue-500;
    font-weight: 700;
}

.name-tag-blue {
    color: $eastern-blue-500;
}
.name-tag-green {
    color: $amulet-green-400;
}

//section artist and 3 artists

.section-for-artist,
.section3artists {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    padding: 2rem 1rem;
    background-color: $black-900;
}

.artist-intoduction {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 0.5rem;
}
//img
.div-artist-img {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1.5rem 0;
}
//tets
.border-for-img {
    display: inline-block;
    border-radius: 30% 70% 30% 70% / 70% 30% 70% 30%;
    /* border: 0.3rem solid $black-600; */
}
//unique color for any artist
#border-for-img-el-double-m {
    padding: 0.5rem;
    border: 0.2rem solid $border-el-double-m;
}
#border-for-img-jackie {
    padding: 0.5rem;
    border: 0.2rem solid $border-jackie;
}
#border-for-img-elKaptn {
    padding: 0.5rem;
    border: 0.2rem solid $border-elKaptn;
}
#border-for-img-mollyGranoli {
    padding: 0.5rem;
    border: 0.2rem solid $border-mollyGranoli;
}
//!animate image artist with ficaption

.artist-img {
    display: block;
    min-width: 10rem;
    min-height: 10rem;
    width: 100%;
    height: auto;
    cursor: pointer;
    border-radius: 30% 70% 30% 70% / 70% 30% 70% 30%;
    animation: expandImage 0s ease-in-out forwards;
}

@keyframes expandImage {
    0% {
        opacity: 0;
        border-radius: 0%;
    }
    100% {
        opacity: 1;
        width: 100%;
        height: auto;
        border-radius: 30% 70% 30% 70% / 70% 30% 70% 30%;
    }
}
.div-inner-img {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 1rem;
    transition: 0.1s ease-in-out;
    border-radius: 30% 70% 30% 70% / 70% 30% 70% 30%;

    &:hover {
        transform: scale(1.1);
    }
}
//img link to unsplash
.link-to-releases {
    text-decoration: none;
    color: $grey-600;
    cursor: pointer;
}
.unsplash-img-link {
    text-decoration: none;
    color: $grey-600;
    font-size: 0.8rem;
}
#unsplash-img-margin,
#unsplash-img-margin2,
#unsplash-img-margin3 {
    margin-left: 1rem;
    margin-top: 0.5rem;
    color: $grey-600;
    animation: expandImage 0.5s ease-in-out forwards;
}
.link-to-music {
    width: auto;
    height: auto;
    text-decoration: none;
    color: $grey-600;
}

//end img things
.h-section-for-artist,
.h-section3 {
    font-size: 1.4rem;
    line-height: 2.8rem;
    text-transform: uppercase;
    width: 100%;
    font-weight: 800;
    color: $white;
    width: auto;
    transition: 0.2s ease-in-out;
    margin-bottom: 0.5rem;
    &:hover,
    :focus,
    :active {
        color: $eastern-blue-500;
    }
}

.short-description-above-img {
    color: $grey-500;
    font-size: 1.2rem;
    font-weight: 400;
}

.name-tag-red {
    color: $red-300;
    font-weight: 800;
}

.div-for-whole-description {
    margin-top: 1.5rem;
}
.p_section-for-artist-mini-description,
.p_section3-mini-description {
    font-size: 0.8rem;
    color: $grey-600;
}

.p_section-for-artist {
    font-size: 1rem;
    line-height: 2rem;
}
//hidden p only for 1024px scrrens and bigger
.p_section-for-artist-hidden {
    display: none;
}

//btn 1
.div-btn-cta {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 2rem;
    gap: 2rem;
}

.inner-div-btn-cta {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    background-color: transparent;
}

.link-for-cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    background-color: $grey-700;
    color: $white;
    width: 14rem;
    height: auto;
    /*  margin: 1rem 0 */
    font-size: 1rem;
    font-weight: 700;
    transition: 0.1s ease-in-out;

    &:hover,
    :focus,
    :active {
        outline: none;
        transform: scale(1.1);
        background-color: $grey-600;
        color: $black-900;
    }
}
// btn chooser artists
//btn chooser
.artist-div {
    display: none;
}

.artist-div.active {
    display: block;
}
.btn-artists:focus {
    outline: 0.2rem solid $amulet-green-400;
}

.btns-chooser {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    gap: 0.5rem;
    padding: 0 0 0.5rem 0;
    margin: 1.5rem 0;
    background-color: $black-900;
    border-bottom: 0.4rem solid $black-700;
    position: sticky;
    top: 3.5rem;
    z-index: 1;
}

.btns-chooser.reached {
    position: static;
}
.hr-line-under-btns {
    border: none;
    width: 100%;
    background-color: $grey-700;
    height: 0.05rem;
    opacity: 0.1;
    margin-bottom: 1.5rem;
}

.btn-artists {
    width: 33%;
    height: 2.5rem;
    border-radius: 0.5rem;
    outline: none;
    cursor: pointer;
    border: none;
    font-weight: 600;
    background-color: transparent;
    color: $white;
    border: 0.1rem $grey-600;
    font-size: 0.8rem;
    opacity: 0.5;
    transition: 0.2s ease-in-out;
    &:hover {
        background-color: $grey-700;
        color: $white;
    }
}
.btn-artists.active {
    background-color: $amulet-green-400;
    color: $black-900;
    opacity: 1;
}

//footer new
 // footer

//top footer

.hr-line-footer {
    width: 100%;
    border: none;
    background-color: $grey-600;
    height: 0.05rem;
    opacity: 0.1;
}
footer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    padding: 0rem 1rem;
}
.footer-top {
    display: flex;
    flex-direction: column; 
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    padding: 2rem 1rem;

}
.footer-a-1,
.footer-a-2 {
    display: flex;
    flex-direction: column;
    width: auto;
}

.nav-a-footer {
    color: $amulet-green-400;
    font-weight: 800;
    text-transform: uppercase;
    font-size: 1.4rem;
    text-decoration: none;
    transition: 0.1s ease-in-out;
    padding: 0.5rem; 
    &:hover {
        transform: scale(1.05);
        color: $grey-600;
    }
}
//footer form -newsletter
.description-and-email-input {
