    height: auto;
}
.email-input-description {
    padding: 2rem 0 1rem 0;
    font-size: 1.2rem;
    font-weight: 400;
    color: $grey-600;

}
.input-wth-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
