.send-btn {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
   border-radius: 50%;
    border: 0.1rem solid ;
    background-color: transparent;
    color: $eastern-blue-500;
    padding: 0;
    margin-left: 0;
    width: auto;
    height: auto;
    transition: 0.2s ease-in-out;
    font-size: 1.4rem;
    padding: 0.5rem;
    z-index: 1;
    cursor: pointer;
    &:hover {
        transform: scale(1.1);
        color: $grey-500;
    }
    &:focus {
        transform: scale(1.1);
        color: $amulet-green-400;
    }
}


//bottom footer

.footer-bottom {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    height: auto;
}

.footer-p-and-nav {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    height: auto;
    gap: 1rem;
    padding-top: 1rem;
}

.nav-a-footer-social {
    padding: 0 0.5rem;
}

#bottom-footer-social-1,
#bottom-footer-social-2,
#bottom-footer-social-3,
#bottom-footer-social-4,
#bottom-footer-social-5,
#bottom-footer-social-6,
#bottom-footer-social-7,
#bottom-footer-social-8,
#bottom-footer-social-9 {
    font-size: 1.4rem;
    color: $grey-500;
    cursor: pointer;

    &:hover {
        transition: 0.1s ease-in-out;
        transform: scale(1.2);
        color: $amulet-green-400;
    }
}
.time-and-rights {
    font-size: 1rem;
    color: $grey-500;
    font-size: 600;
}
//about me
.about-me {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: auto;
    padding-top: 2rem;
}
#description-about-me {
    font-size: 1rem;
    width: 80%;
    padding-bottom: 1rem;
}
#anchor-description-about-me {
    text-decoration: none;
    color: $grey-500;
    font-weight: 800;
    transition: 0.1s ease-in-out;
    &:hover {
        color: $eastern-blue-500;
    }
}

#my-name-is {
    font-size: 1.5rem;
    padding: 0.5rem 0;
}
.my-social {
    font-size: 3rem;
    color: $ming-blue-600;
    transition: 0.1s ease-in-out;
    &:hover {
        color: $amulet-green-400;
    }
}

//media queries for 375px screens

@media (min-width: 375px) {
    .name-and-logo {
        gap: 2rem;
        line-height: 4.2rem;
    }
    .h1-main-name {
        font-size: 2.1rem;
    }
    .img-square-section {
        width: 2.1rem;
        height: 2.1rem;
    }
}

