//media q for 1024px

@media (min-width: 1024px) {
    //nav top
    // hide mobile menu and nav for desktop row - 1024+
    .top-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: $shadow-black-transparent;
        backdrop-filter: blur(0.5rem);
        width: 100%;
        height: 3.5rem;
        padding: 0 2rem;
        position: fixed;
        z-index: 2;
    }
    #logo {
        display: block;
        width: 1.75rem;
        height: 1.75rem;
        font-size: 1.75rem;
        transition: ease-in-out 0.2s;
        cursor: pointer;
    }
    #chevron-to-show-social-menu {
        display: block;
        width: 1.75rem;
        height: 1.75rem;
        font-size: 1.75rem;
        transition: ease-in-out 0.2s;
        cursor: pointer;
        border-radius: 50%;
        &:hover {
            color: $grey-500;
            background-color: $black-600;
            border-radius: 50%;
        }
        &.rotate {
            transform: rotate(180deg);
        }
    }

    #dropdown-menu-icon {
        display: none;
    }

    .nav-desktop {
        display: block;
        width: 80%;
    }
    .desktop_menu-a {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: row;
        gap: 1.5rem;
        padding: 1rem 0rem;
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: 800;
        margin: 0;
    }

    .nav-a-desktop-menu {
        text-decoration: none;
        color: $grey-500;
        transition: 0.1s ease-in-out;
